<template>
    <div class="main">
        <mt-header fixed :title="mainTitle" style="box-shadow: 0 0 8px rgba(0, 0, 0, .3);"></mt-header>
        <div class="main-center">
            <keep-alive v-if="$route.meta.keepAlive">
                <router-view></router-view>
            </keep-alive>
            <router-view v-else></router-view>
        </div>
        <mt-tabbar v-model="title" class="tabbar">
            <mt-tab-item id="home" @click.native="selectTab">
                <i class="iconfont icon-article-s"></i>
                <div class="bar-text">文章</div>
            </mt-tab-item>
            <mt-tab-item id="collection" @click.native="selectTab">
                <i class="iconfont icon-zuopintuijian"></i>
                <div class="bar-text">作品</div>
            </mt-tab-item>
            <mt-tab-item id="me" @click.native="selectTab">
                <i class="iconfont icon-wo1"></i>
                <div class="bar-text">我的</div>
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                title: 'home'
            };
        },
        methods: {
            selectTab() {
                this.$router.push({name: this.title});
            }
        },
        mounted: function () {

        },
        computed: {
            mainTitle: function () {
                return this.title.toLocaleUpperCase();
            }
        },
        components: {},
    }
</script>
<style lang="less">
    .main-center {
        background: #fff;
        margin-top: 40px;
        margin-bottom: 54px;
        //min-height: calc(~'100vh - 94px');
        height: calc(~'100vh - 94px');
        overflow: auto;
    }

    .tabbar {
        background: #fff;
        position: fixed;
        box-shadow: 0 0 8px rgba(0, 0, 0, .3);
        .bar-text {
            margin-top: 2px;
        }
        .iconfont {
            font-size: 26px;
        }
        > .mint-tab-item.is-selected {
            background: none;
        }
    }
</style>


